import React from 'react';
import './css/quickbardetail.css';
import cookie from 'react-cookies';
import riyongping from '../../assets/QQPenguin_gg/riyongping.png';
import diamond from '../../assets/QQPenguin_gg/diamond.png';
import jinyuanbao from '../../assets/QQPenguin_gg/yuanbao.png';
import pengshiqi from '../../assets/QQPenguin_gg/pengshiqi.png';
import baobaojinshui from '../../assets/QQPenguin_gg/baobaojinshui.png';
import icecream from '../../assets/QQPenguin_gg/icecream.png';
import mooncake from '../../assets/QQPenguin_gg/mooncake.png';
import zhuangtou from '../../assets/QQPenguin_gg/work/1/2.gif';
import wagong from '../../assets/QQPenguin_gg/work/1/4.gif';
import huanhundan from '../../assets/QQPenguin_gg/huanhundan.png';
import baicaodan from '../../assets/QQPenguin_gg/baicaodan.png';
import xiaoxue_laoji from '../../assets/QQPenguin_gg/xiaoxue_laoji.png';
import xiaoxue_tiyu from '../../assets/QQPenguin_gg/xiaoxue_tiyu.png';

class QuickbarDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = { menu: [1, 0], item: [1, 0], num1: 1, num2: 1 };
  }
  componentDidMount() {
    this.setState({ num1: 1, num2: 1 });
  }
  buy = e => {
    e.preventDefault();
    this.setState({ menu: [0, 1] });
  };

  chooseitem1 = () => {
    this.setState({ item: [1, 0] });
  };

  chooseitem2 = () => {
    this.setState({ item: [0, 1] });
  };

  feed = () => {
    this.props.feed();
    this.props.shut();
    const hungery = parseInt(cookie.load('hungery'));
    let inFifteenMinutes = new Date(
      new Date().getTime() + 7 * 24 * 3600 * 1000,
    ); //
    if (!!this.state.item[0])
      cookie.save('hungery', hungery + 90 < 800 ? hungery + 90 : 800, {
        path: '/',
        expires: inFifteenMinutes,
      });
    else
      cookie.save('hungery', hungery + 180 < 800 ? hungery + 180 : 800, {
        path: '/',
        expires: inFifteenMinutes,
      });
  };

  medicine = () => {
    this.props.medicine();
    this.props.shut();
  };

  work = () => {
    clearInterval(global.pet.Interval3);
    this.props.work1();
    this.props.shutwork();
    let inFifteenMinutes = new Date(
      new Date().getTime() + 7 * 24 * 3600 * 1000,
    ); //
    cookie.save('now', '', { path: '/', expires: inFifteenMinutes });
    global.pet.Interval3 = setInterval(
      () => {
        this.normal();
        let inFifteenMinutes = new Date(
          new Date().getTime() + 7 * 24 * 3600 * 1000,
        ); //
        cookie.save('now', '', { path: '/', expires: inFifteenMinutes });
      },
      !!this.state.item[0] ? 1000 * 60 * 30 : 1000 * 60 * 60,
    );
  };
  normal = () => {
    this.props.normal();
  };
  study = () => {
    clearInterval(global.pet.Interval3);
    this.props.study1();
    this.props.shutwork();
    let inFifteenMinutes = new Date(
      new Date().getTime() + 7 * 24 * 3600 * 1000,
    ); //
    cookie.save('now', '', { path: '/', expires: inFifteenMinutes });
    global.pet.Interval3 = setInterval(
      () => {
        this.normal();
        let inFifteenMinutes = new Date(
          new Date().getTime() + 7 * 24 * 3600 * 1000,
        ); //
        cookie.save('now', '', { path: '/', expires: inFifteenMinutes });
      },
      !!this.state.item[0] ? 1000 * 60 * 30 : 1000 * 60 * 45,
    );
  };

  shower = () => {
    this.props.shower();
    this.props.shut();
    const clean = parseInt(cookie.load('clean'));
    let inFifteenMinutes = new Date(
      new Date().getTime() + 7 * 24 * 3600 * 1000,
    ); //
    if (this.state.item[0])
      cookie.save('clean', clean + 360 < 1000 ? clean + 360 : 1000, {
        path: '/',
        expires: inFifteenMinutes,
      });
    else
      cookie.save('clean', clean + 540 < 1000 ? clean + 540 : 1000, {
        path: '/',
        expires: inFifteenMinutes,
      });
  };

  changeEvent1 = e => {
    let value = e.target.value.replace(/[^\d]/, '');
    if (value === '') value = 1;
    this.setState({ num1: value });
  };

  changeEvent2 = e => {
    let value = e.target.value.replace(/[^\d]/, '');
    if (value === '') value = 1;
    this.setState({ num2: value });
  };

  leftArrow1 = () => {
    this.setState({ item: [1, 0] });
    if (this.state.num1 < 99) this.setState({ num1: this.state.num1 + 1 });
  };

  leftArrow2 = () => {
    this.setState({ item: [0, 1] });
    if (this.state.num2 < 99) this.setState({ num2: this.state.num2 + 1 });
  };

  rightArrow1 = () => {
    this.setState({ item: [1, 0] });
    if (this.state.num1 > 1) this.setState({ num1: this.state.num1 - 1 });
  };

  rightArrow2 = () => {
    this.setState({ item: [0, 1] });
    if (this.state.num2 > 1) this.setState({ num2: this.state.num2 - 1 });
  };

  render() {
    return (
      <div>
        {this.state.menu[0] && !(this.props.work || this.props.study) ? (
          <div
            className="detailmenu1"
            style={{
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div className="title">:</div>
            <div className="right-title-btn" onClick={this.props.shut}></div>
            <div className="itemlist">
              <div className="itemLeft"></div>
              <img src={riyongping}></img>
              <div className="item">
                {this.props.type},
                <a href="#" onClick={this.buy}>
                  
                </a>
                
              </div>
              <div className="itemRight"></div>
            </div>
            <div className="footer">
              <img src={diamond}></img>
              <div>
                <a href="#"></a>,
              </div>
              <div></div>
            </div>
          </div>
        ) : null}
        {this.state.menu[1] && this.props.type === '' ? (
          <div
            className="detailmenu2"
            style={{
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div className="title">
              {this.props.type},{this.props.type}
            </div>
            <div className="right-title-btn" onClick={this.props.shut}></div>
            <div className="choosItem">:</div>
            <button
              className="shopping"
              onClick={() => alert(``)}
            >
              &nbsp;&nbsp;
            </button>
            <br />
            <div
              className="item1"
              style={{
                border: this.state.item[0] ? `2px solid` : null,
                borderColor: this.state.item[0] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[0]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={pengshiqi} onClick={this.chooseitem1}></img>
              <div className="detail">
                
                <br />
                :+360
                <br />
                : 10
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow1}></div>
                <input
                  value={this.state.num1}
                  onChange={this.changeEvent1}
                ></input>
                <div className="numLeft" onClick={this.rightArrow1}></div>
              </div>
            </div>
            <div
              className="item2"
              style={{
                border: this.state.item[1] ? `2px solid` : null,
                borderColor: this.state.item[1] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[1]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={baobaojinshui} onClick={this.chooseitem2}></img>
              <div className="detail">
                
                <br />
                :+540
                <br />
                : 15
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow2}></div>
                <input
                  value={this.state.num2}
                  onChange={this.changeEvent2}
                ></input>
                <div className="numLeft" onClick={this.rightArrow2}></div>
              </div>
            </div>
            <div className="choosItem"></div>
            <br />
            <div className="choosPay">
              <label>
                <input
                  name="pay"
                  type="radio"
                  value="1"
                  defaultChecked={true}
                />
                
              </label>
              <label>
                <input name="pay" type="radio" value="2" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="3" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="4" />
                
              </label>
            </div>
            <div className="virtualLine"></div>
            <div className="yuanbao">
              <img src={jinyuanbao}></img>
              250
            </div>
            <button className="pay" onClick={this.shower}>&nbsp;&nbsp;&nbsp;&nbsp;</button>
          </div>
        ) : null}
        {this.state.menu[1] && this.props.type === '' ? (
          <div
            className="detailmenu2"
            style={{
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div className="title">
              {this.props.type},{this.props.type}
            </div>
            <div className="right-title-btn" onClick={this.props.shut}></div>
            <div className="choosItem">:</div>
            <button
              className="shopping"
              onClick={() => alert(``)}
            >
              &nbsp;&nbsp;
            </button>
            <br />
            <div
              className="item1"
              style={{
                border: this.state.item[0] ? `2px solid` : null,
                borderColor: this.state.item[0] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[0]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={icecream} onClick={this.chooseitem1}></img>
              <div className="detail">
                
                <br />
                :+90
                <br />
                : 5
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow1}></div>
                <input
                  value={this.state.num1}
                  onChange={this.changeEvent1}
                ></input>
                <div className="numLeft" onClick={this.rightArrow1}></div>
              </div>
            </div>
            <div
              className="item2"
              style={{
                border: this.state.item[1] ? `2px solid` : null,
                borderColor: this.state.item[1] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[1]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={mooncake} onClick={this.chooseitem2}></img>
              <div className="detail">
                
                <br />
                :+180
                <br />
                : 10
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow2}></div>
                <input
                  value={this.state.num2}
                  onChange={this.changeEvent2}
                ></input>
                <div className="numLeft" onClick={this.rightArrow2}></div>
              </div>
            </div>
            <div className="choosItem"></div>
            <br />
            <div className="choosPay">
              <label>
                <input
                  name="pay"
                  type="radio"
                  value="1"
                  defaultChecked={true}
                />
                
              </label>
              <label>
                <input name="pay" type="radio" value="2" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="3" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="4" />
                
              </label>
            </div>
            <div className="virtualLine"></div>
            <div className="yuanbao">
              <img src={jinyuanbao}></img>
              250
            </div>
            <button className="pay" onClick={this.feed}>&nbsp;&nbsp;&nbsp;&nbsp;</button>
          </div>
        ) : null}
        {this.state.menu[1] && this.props.type === '' ? (
          <div
            className="detailmenu2"
            style={{
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div className="title">
              {this.props.type},{this.props.type}
            </div>
            <div className="right-title-btn" onClick={this.props.shut}></div>
            <div className="choosItem">:</div>
            <button
              className="shopping"
              onClick={() => alert(``)}
            >
              &nbsp;&nbsp;
            </button>
            <br />
            <div
              className="item1"
              style={{
                border: this.state.item[0] ? `2px solid` : null,
                borderColor: this.state.item[0] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[0]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={huanhundan} onClick={this.chooseitem1}></img>
              <div className="detail">
                 (
                <br />
                )
                <br />
                : 400
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow1}></div>
                <input
                  value={this.state.num1}
                  onChange={this.changeEvent1}
                ></input>
                <div className="numLeft" onClick={this.rightArrow1}></div>
              </div>
            </div>
            <div
              className="item2"
              style={{
                border: this.state.item[1] ? `2px solid` : null,
                borderColor: this.state.item[1] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[1]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={baicaodan} onClick={this.chooseitem2}></img>
              <div className="detail">
                 (
                <br />
                )
                <br />
                : 240
              </div>
              <div className="num">
                :
                <div className="numRight" onClick={this.leftArrow2}></div>
                <input
                  value={this.state.num2}
                  onChange={this.changeEvent2}
                ></input>
                <div className="numLeft" onClick={this.rightArrow2}></div>
              </div>
            </div>
            <div className="choosItem"></div>
            <br />
            <div className="choosPay">
              <label>
                <input
                  name="pay"
                  type="radio"
                  value="1"
                  defaultChecked={true}
                />
                
              </label>
              <label>
                <input name="pay" type="radio" value="2" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="3" />
                Q
              </label>
              <label>
                <input name="pay" type="radio" value="4" />
                
              </label>
            </div>
            <div className="virtualLine"></div>
            <div className="yuanbao">
              <img src={jinyuanbao}></img>
              250
            </div>
            <button className="pay" onClick={this.medicine}>&nbsp;&nbsp;&nbsp;&nbsp;</button>
          </div>
        ) : null}
        {this.props.work ? (
          <div
            className="detailmenu3"
            style={{
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div
              className="right-title-btn"
              onClick={this.props.shutwork}
            ></div>
            <div className="title">
              ,,!
            </div>
            <div
              className="item1"
              style={{
                border: this.state.item[0] ? `2px solid` : null,
                borderColor: this.state.item[0] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[0]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={zhuangtou} onClick={this.chooseitem1}></img>
              <div className="detail">
                
                <br />
                +10*3
              </div>
            </div>
            <div
              className="item2"
              style={{
                border: this.state.item[1] ? `2px solid` : null,
                borderColor: this.state.item[1] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[1]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={wagong} onClick={this.chooseitem2}></img>
              <div className="detail">
                
                <br />
                +10*3
              </div>
            </div>
            <div className="choosItem">
              
              <br />
            </div>
            <div className="choosTime">
              <label>
                <input
                  name="pay"
                  type="radio"
                  value="1"
                  defaultChecked={true}
                />
                0.5
              </label>
              <label>
                <input name="pay" type="radio" value="2" />
                1
              </label>
            </div>
            <button className="work" onClick={this.work}>
              &nbsp;&nbsp;&nbsp;&nbsp;
            </button>
            <a
              href="#"
              onClick={() => alert(``)}
            >
              
            </a>
          </div>
        ) : null}
        {this.props.study ? (
          <div
            className="detailmenu3"
            style={{
              height: `166px`,
              transform: `translate(calc(100vw - 380px + ${this.props.x}px),calc(100vh - 300px + ${this.props.y}px))`,
            }}
          >
            <div
              className="right-title-btn"
              onClick={this.props.shutwork}
            ></div>
            <div className="title">
              !
            </div>
            <div
              className="item1"
              style={{
                marginTop: '10px',
                border: this.state.item[0] ? `2px solid` : null,
                borderColor: this.state.item[0] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[0]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={xiaoxue_laoji} onClick={this.chooseitem1}></img>
              <div className="study_detail">
                
                <br />
                : 8
                <br />
                :+5
              </div>
            </div>
            <div
              className="item2"
              style={{
                marginTop: '10px',
                border: this.state.item[1] ? `2px solid` : null,
                borderColor: this.state.item[1] ? `rgb(52, 220, 225)` : null,
                boxShadow: this.state.item[1]
                  ? `2px 2px 5px rgb(52, 220, 225)`
                  : null,
              }}
            >
              <img src={xiaoxue_tiyu} onClick={this.chooseitem2}></img>
              <div className="study_detail">
                
                <br />
                : 8
                <br />
                :+5
              </div>
            </div>
            <button
              className="work"
              style={{
                marginTop: '15px',
              }}
              onClick={this.study}
            >
              &nbsp;&nbsp;&nbsp;&nbsp;
            </button>
          </div>
        ) : null}
      </div>
    );
  }
}
export default QuickbarDetail;
